import React from 'react';
import './header.scss';
import Navbar from './Navbar';

const Header = () => {
  return (
    <>
      <div className="header">
        <Navbar />

        <div className="hero">
          <div className="logo">
            <img src="/img/m.png" alt />
            <div>
              {' '}
              <h1>
                TOWN'S CAFE.
                <br />
                The Pure Soul of FOOD.
              </h1>
              <a href="#" className="btn">
                Show Menu
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;
