import React from 'react';
import './menu.scss';
import { spl, veg, nonveg } from '../data/beverages.js';

const Menu = () => {
  return (
    <section className="menu-head">
      <h2>Pick Your Food.</h2>
      <div className="menu">
        <div className="menu-list">
          <table>
            <tr>
              <th className="head-item">Town's Cafe Special</th>
              <th className="head-price"></th>
            </tr>
            {spl.map((b) => (
              <tr>
                <td className="item">{b.item}</td>
                <td className="price">{b.price}</td>
              </tr>
            ))}
          </table>
        </div>

        <div className="menu-list">
          <table>
            <tr>
              <th className="head-item">Veg Main Course</th>
              <th className="head-price"></th>
            </tr>
            {veg.map((b) => (
              <tr>
                <td className="item">{b.item}</td>
                <td className="price">{b.price}</td>
              </tr>
            ))}
          </table>
        </div>

        <div className="menu-list">
          <table>
            <tr>
              <th className="head-item">Non Veg</th>
              <th className="head-price"></th>
            </tr>
            {nonveg.map((b) => (
              <tr>
                <td className="item">{b.item}</td>
                <td className="price">{b.price}</td>
              </tr>
            ))}
          </table>
        </div>
      </div>
      <h4>
        {' '}
        Above List contains only Popular Food but we have much you can order{' '}
        <a className="btn">Full Menu</a>{' '}
      </h4>
    </section>
  );
};

export default Menu;
